.optional {
  color: green;
}

.check-box {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  height: 15px;
  border: 2px solid var(--border-color);
  border-radius: 4px;
  cursor: pointer;
  vertical-align: middle;
  margin-top: -2px; // Adjust this value to fine-tune vertical alignment

  input[type="checkbox"] {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    cursor: pointer;
    z-index: 1;

    &:checked + .checkmark {
      background: var(--active-color);
      border-color: var(--active-color);

      &::after {
        content: "";
        position: absolute;
        left: 50%;
        top: 45%;
        width: 5px;
        height: 10px;
        border: solid var(--checkmark-color);
        border-width: 0 2px 2px 0;
        transform: translate(-50%, -50%) rotate(45deg);
      }
    }
  }

  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
